/* ===============================
    LESS VARIABLES
   =============================== */
#meteor-blog {
  padding-top: 20px;
  padding-bottom: 40px;
}
#meteor-blog.blog-list .thumbnail {
  margin-top: 20px;
}
#meteor-blog.blog-list article.post {
  border-bottom: 1px solid #EEE;
  padding-bottom: 40px;
  margin-bottom: 40px;
}
#meteor-blog.blog-list article.post:last-child {
  border-bottom: none;
}
#meteor-blog.blog-list article.post h2 {
  font-size: 32px;
  line-height: 45px;
  margin-bottom: 8px;
  padding-right: 0px;
}
@media (min-width: 768px) {
  #meteor-blog.blog-list article.post h2 {
    padding-right: 100px;
  }
}
#meteor-blog.blog-list article.post h2 a:hover {
  text-decoration: none;
}
#meteor-blog.blog-list article.post .post-meta {
  color: #bbb;
  margin-bottom: 50px;
}
#meteor-blog.blog-list article.post .post-meta > a {
  color: #bbb;
}
#meteor-blog.blog-list article.post .post-meta > a:hover {
  color: #bbb;
}
#meteor-blog.blog-list article.post .post-meta .post-tags {
  display: inline;
}
#meteor-blog.blog-list article.post .post-meta .post-tags p {
  display: inherit;
}
#meteor-blog.blog-single {
  padding-top: 0px;
}
#meteor-blog.blog-single .mb-title {
  font-size: 50px;
  margin-top: 50px;
}
#meteor-blog.blog-single .mb-featured-image-container .mb-aspect-ratio-fill {
  padding-bottom: 40.1%;
}
#meteor-blog.blog-single .mb-featured-image-container .mb-scaleImage {
  margin-top: 20px;
}
#meteor-blog.blog-single .mb-featured-image-container .mb-background-title {
  text-shadow: black 0.2em 0.2em 0.2em;
  color: white;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 150px;
  position: absolute;
  z-index: 10;
}
#meteor-blog.blog-single .mb-featured-image-container img.featured-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
  margin: auto;
  max-width: 100%;
  box-sizing: border-box;
}
#meteor-blog.blog-single .mb-featured-image-container .mg-aspectRatioPlaceholder {
  position: relative;
  width: 100%;
  margin: 0 auto;
}
#meteor-blog.blog-single article.post {
  border-bottom: 1px solid #EEE;
  padding-bottom: 10px;
  margin-bottom: 30px;
  /* MEDIUM-INSERT PLUGIN */
}
#meteor-blog.blog-single article.post .mediumInsert {
  width: 100%;
  margin: -1em 0 0 0 !important;
}
#meteor-blog.blog-single article.post .mediumInsert img {
  max-width: 100%;
}
#meteor-blog.blog-single article.post .mediumInsert.small {
  float: left;
  width: 33.333333%;
  margin-right: 10px !important;
}
#meteor-blog.blog-single article.post .mediumInsert.small img {
  margin: 15px auto;
}
#meteor-blog.blog-single article.post .post-meta {
  color: #bbb;
  margin-bottom: 50px;
}
#meteor-blog.blog-single article.post .post-meta > a {
  color: #bbb;
}
#meteor-blog.blog-single article.post .post-meta > a:hover {
  color: #bbb;
}
#meteor-blog.blog-single article.post .post-meta .post-tags {
  display: inline;
}
#meteor-blog.blog-single article.post .post-meta .post-tags p {
  display: inherit;
}
#meteor-blog.blog-single article.post h2 {
  font-size: 40px;
  line-height: 48px;
  margin-bottom: 8px;
}
#meteor-blog.blog-single article.post h2 a:hover {
  text-decoration: none;
}
#meteor-blog.blog-single article.post img {
  border: 1px solid rgba(0, 0, 0, 0.5);
  margin: 30px auto;
  display: block;
}
#meteor-blog.blog-single article.post code {
  color: inherit;
}
#meteor-blog.blog-single .blog-back-link {
  margin-bottom: 20px;
}
#meteor-blog.blog-single .blog-back-link i {
  margin-right: 10px;
}
#meteor-blog.blog-single .blog-back-link a:hover {
  text-decoration: none;
}
#meteor-blog.blog-single .share-buttons {
  float: right !important;
}
#meteor-blog.blog-single .share-buttons .btn-facebook,
#meteor-blog.blog-single .share-buttons .btn-twitter {
  font-size: inherit;
}
#meteor-blog.blog-single .share-buttons i {
  margin-right: 10px;
}
